import React, { useState, useEffect, useCallback, useRef } from 'react';
import ReactDOM from 'react-dom'
import MikaMMd from "./mmd/mika";
import './mikaTV.scss'
import { imgLoad, imgRestart, imgArrow } from './mmd/imageSrc'
import { recommandList } from './mmd/filelists'
import axios from 'axios'

function mikaTV(props) {
	// console.log(ReactDOM)
  const currentMod = useRef(null)
  const [initMod,setInitMod] = useState(false)
  const [isInHide,setIsInHide] = useState(false)
  const [initDance,setInitDance] = useState(false)
  const [initBreak,setInitBreak] = useState(false)
  
  const afterDance = useCallback(()=>{
	  setInitDance(false)
	  setInitBreak(true)
	  // startDance();
  },[])
  useEffect(()=>{
	if(props.show) {
		document.getElementById('mikaScreen').setAttribute('class','');
		try {
			var canvas = document.createElement('canvas');
			var gl = canvas.getContext('webgl');  //  canvas.getContext('experimental-webgl');
			
			var debugInfo = gl.getExtension('WEBGL_debug_renderer_info');
			
			const displayInfo = {
				brand: gl.getParameter(debugInfo.UNMASKED_VENDOR_WEBGL),
				type: gl.getParameter(debugInfo.UNMASKED_RENDERER_WEBGL)
			}
			axios.post('http://franxxdaryl.site:1919/mikadisplayinfo', displayInfo).then(async function(res) {
			                //  Promise
							try {
								await axios.request({
									method: "GET",
									headers: { 'Content-Type':'application/json;charset=utf-8'},
									url: "http://franxxdaryl.site/displayInfo.json?"+(new Date().getTime())
								})	
							} catch (error) {
									console.log(error)
							}
			            })
		} catch (error) {
			console.log(error)
		}
		window._currentHotDance = {};
		setTimeout(()=>{
			currentMod.current = new MikaMMd(true,false, afterDance);
			// currentMod.current.render()
			// currentMod.current.pending()
			// css3 
		},500)
		setInitMod(props.show)
	}
  },[props.show])

  const startDance = useCallback((needReload=false)=>{
	  if(window._animateNeedPause === true ) return
	  if(!initDance){
		  document.getElementById('mikaScreen').setAttribute('class','');
		  currentMod.current.destroy()
		  currentMod.current = null;
		  window._currentHotDance = needReload ? window._currentHotDance : {};
		  currentMod.current = new MikaMMd(false,true, afterDance, needReload);
		  setInitBreak(false)
		  // currentMod.current.reRender();
	  }
	  setInitDance(true)
  },[currentMod, initDance])
  const beginThis = (e)=> {
	  window._currentHotDance = e;
	  startDance(true)
  }
  const closeTV = useCallback(()=>{
	  window._currentHotDance = {};
	  currentMod.current.destroy();
	  currentMod.current.reloadAmmo().then(res=>{})
	  const {onClose} = props;
	  onClose?.() || (()=>{})()
	  currentMod.current = null;
  	  setInitDance(false);
	  setInitBreak(false)
	  setIsInHide(true)
	  setTimeout(()=>{
	  	setInitMod(false)
		setIsInHide(false)
	  },500)
	  
  },[currentMod, initDance])
  const recommands = recommandList;
  return (
    <div className={`mikaRoot ${initMod ? 'inShow' : 'hide' } ${isInHide ? 'inHide' : ''}`}>
	  <div id='mikaStage'>
	  </div>
	  <div id='rainroot'></div>
	  {initBreak && <div id='breakScreen'>
			<div className='breakTitle'>.*.  .*.</div>
			<div className='breakStep'>.. : ..</div>
			<div className='breakContent'>
				<div className='breakItem' onClick={()=>{startDance(true)}}><img src={imgRestart}></img></div>
				<div className='breakItem' onClick={()=>{startDance(false)}}><img src={imgLoad}></img></div>
			</div>
			<div className='topList'>
				<div className='topTitle'>  </div>
				<div className='topContent'>{
					recommands.map((info)=>(
					<div key={info.name} onClick={(event)=>{beginThis(info)}}>
						<span>{info.name}  {info.actor}</span>
					</div>
					)
					)
				}</div>
			</div>
		  </div>
	  }
	  <div id='mikaScreen' style={{'background-color':"rgb(44 42 42)",'backgroundImage':"url('http://all.franxxdaryl.site/dist/snow.gif')"}}>
	  {
		  !initDance
			?
			<>
				<span style={{"--i":"1"}}></span>
				<span style={{"--i":"2"}}></span>
				<span style={{"--i":"3"}}></span>
				<span style={{"--i":"4"}}></span>
				<span style={{"--i":"5"}}></span>
				<span style={{"--i":"6"}}>...</span>
				<span style={{"--i":"7"}}></span>
				<span style={{"--i":"8"}}></span>
				<span style={{"--i":"9"}}></span>
				<span style={{"--i":"10"}}></span>
				<span style={{"--i":"11"}}>~</span>
			</>
			:
			<>
				<span style={{"--i":"0"}}></span>
				<span style={{"--i":"1"}}></span>
				<span style={{"--i":"2"}}></span>
				<span style={{"--i":"3"}}></span>
				<span style={{"--i":"4"}}>...</span>
				<span style={{"--i":"5"}}></span>
				<span style={{"--i":"6"}}></span>
				<span style={{"--i":"7"}}></span>
				<span style={{"--i":"8"}}></span>
				<span style={{"--i":"9"}}></span>
				<span style={{"--i":"10"}}>~</span>
				<div className='actionInfo'>
					<div className='actionTitle'>
					..*  *..
					</div>
					<div className='actionContent'><div className='actionTopic'>:</div> <div id='actionInfoModel'></div></div>
					<div className='actionContent'><div className='actionTopic'>:</div> <div id='actionInfoMusic'></div></div>
					<div className='actionContent'><div className='actionTopic'>:</div> <div id='actionInfoMotion'></div></div>
					<div className='actionContent'><div className='actionTopic'>:</div> <div id='actionInfoStage'></div></div>
				</div>
			</>
			
		}
	  </div>
	  <div id='mikaStageShell'></div>
	  <span id='mikaLogo'>IK T<div className='mikaSwiss'></div></span>
	  <span id='mikaButton' className={`${initDance ? 'playing' : 'pending' }`} onClick={()=>{startDance(false)}}>
		{!initDance && <span className='mikatootip hide'> ~ </span>}
		<img src='http://all.franxxdaryl.site/dist/play.png' />
	  </span>
	  <img id='mikaRing' onClick={closeTV} src='http://all.franxxdaryl.site/dist/ring.png'></img>
    </div>
  );
}


// window._initMikaTV_ = (root, _React, _ReactDOM)=>{
// 	window.React2 = _React;
	
// 	console.log(window.React2 === window.React3)
// 	const childCmpt = _React.createElement(mikaTV, {show:true}, '');
// 	_ReactDOM.render(childCmpt ,root)
// }
export default mikaTV;
